<template>
  <div class="app-wrapper app-wrapper--login">
    <div class="login-content__wrapper">
      <div class="login-content">
        <section class="brand-info">
          <h2 class="brand-info__title">链优品-商城管理系统</h2>
          <p class="brand-info__intro">
            comc mall admin 基于vue、element-ui构建开发，实现 COMC
            商城后台管理前端功能，提供一套更优的前端解决方案。
          </p>
        </section>

        <section class="login-main">
          <h3 class="login-title">管理员登陆</h3>
          <el-form
            status-icon
            ref="loginForm"
            :model="loginForm"
            :rules="loginRules"
            @keyup.enter.native="loginFormSubmit()"
          >
            <el-form-item prop="account">
              <el-input
                placeholder="手机号/邮箱"
                v-model="loginForm.account"
              ></el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input
                v-model="loginForm.password"
                type="password"
                placeholder="密码"
              ></el-input>
            </el-form-item>
            <span class="login__forget" @click="forgetHandle">忘记密码?</span>
            <el-form-item>
              <el-button
                type="primary"
                class="login-btn-submit"
                @click="loginFormSubmit"
                :disabled="!isCanSubmit"
                :loading="loading"
                >登录</el-button
              >
            </el-form-item>
          </el-form>
        </section>
      </div>
    </div>
  </div>
</template>
<script>
import { loginByAcount } from "@/api/common";
import { isEmail, isPhone } from "@/utils/validate";

export default {
  name: "Login",
  data() {
    const checkAccount = (rule, value, callback) => {
      if (!value) return callback(new Error("账号不能为空"));

      if (!isEmail(value) && !isPhone(value))
        return callback(new Error("账号格式错误"));

      callback();
    };
    return {
      loginForm: {
        account: "",
        password: ""
      },
      loginRules: {
        account: [{ validator: checkAccount, trigger: "blur" }],
        password: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { min: 6, message: "密码长度不能少于 6 位", trigger: "blur" }
        ]
      },
      loading: false
    };
  },
  computed: {
    isCanSubmit() {
      const { account, password } = this.loginForm;
      return account.length >= 4 && password.length >= 6;
    }
  },
  methods: {
    forgetHandle() {
      this.$router.replace({ name: "forget" });
    },
    // 登陆
    loginFormSubmit() {
      if (this.loading) return;

      this.$refs["loginForm"].validate(valid => {
        if (valid) {
          this.loading = true;
          const { account, password } = this.loginForm;
          const params = { password };
          params[isPhone(account) ? "phone" : "email"] = account;
          loginByAcount(params)
            .then(({ data: { token, message } }) => {
              this.$cookie.set("token", token);
              this.$router.replace({ name: "order" });
              this.$message.success(message);
              this.loading = false;
            })
            .catch(({ response: { data: { message = "登录失败" } } = {} }) => {
              this.loading = false;
              this.$message.error(message);
            });
        }
      });
    }
  }
};
</script>

<style lang="scss">
.app-wrapper {
  &--login {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    overflow: hidden;
    &:before {
      position: fixed;
      top: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      content: "";
      background-image: url(~@/assets/img/login_bg.jpg);
      background-size: cover;
    }
    .login__forget {
      color: #00a1d6;
      margin: 0;
      padding: 0;
      cursor: pointer;
    }
    .login-content {
      min-height: 100%;
      padding: 30px 500px 30px 30px;
      &__wrapper {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        padding: 0;
        margin: 0;
        overflow-x: hidden;
        overflow-y: auto;
        background-color: transparent;
        background-color: rgba(38, 50, 56, 0.6);
      }
    }
    .brand-info {
      margin: 220px 100px 0 90px;
      color: #fff;
      &__title {
        margin: 0 0 22px 0;
        font-size: 48px;
        font-weight: 400;
        text-transform: uppercase;
      }
      &__intro {
        margin: 10px 0;
        font-size: 16px;
        line-height: 1.58;
        opacity: 0.6;
      }
    }
    .login {
      &-main {
        position: absolute;
        top: 0;
        right: 0;
        padding: 150px 60px 180px;
        width: 470px;
        min-height: 100%;
        background-color: #fff;
      }
      &-title {
        font-size: 18px;
      }
      &-btn-submit {
        width: 100%;
        margin-top: 20px;
      }
    }
  }
}
</style>
